.container {
    width: 50%;
    margin: 0 auto;
    padding: 20px;
    text-align: center;
    }
    
    .title {
        font-size: 2rem;
        color: #333;
        margin-bottom: 20px;
    }
    
    .input {
        padding: 10px;
        width: 80%;
        margin-bottom: 10px;
        border: 1px solid #ccc;
        border-radius: 4px;
    }
    
    .search-button {
        padding: 10px 20px;
        background-color: #007bff;
        color: white;
        border: none;
        border-radius: 4px;
        cursor: pointer;
        font-size: 1rem;
    }
    
    .search-button:hover {
        background-color: #0056b3;
    }
    
    .place-details {
        margin-top: 20px;
        padding: 20px;
        border: 1px solid #ccc;
        border-radius: 4px;
        background-color: #f9f9f9;
    }
    
    .place-details h2 {
        font-size: 1.5rem;
        color: #333;
    }
    
    .place-details p {
        font-size: 1.2rem;
        margin: 5px 0;
    }
    
    .place-details ul {
        list-style-type: none;
        padding: 0;
    }
    
    .place-details ul li {
        font-size: 1.2rem;
        margin: 5px 0;
    }
    